html {
  box-sizing: border-box;
  touch-action: manipulation;
  -webkit-tap-highlight-color: transparent;
  -webkit-print-color-adjust: exact;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Nunito", "Arial", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
}

code {
  font-family: Consolas, "Courier New", monospace;
}
.Icon{fill:none;transition:-webkit-transform 0.3s;transition:transform 0.3s;transition:transform 0.3s, -webkit-transform 0.3s}.Icon--infographic{background-color:#999;padding:10px;stroke:#fff;stroke-width:6;width:50px}.Icon--profit{background-color:#29bfd7}.Icon--users{background-color:#bcd74a}.Icon--orders{background-color:#fe9833}.Icon--complaints{background-color:#fa5050}.Icon--dropdown{fill:#555;height:15px;width:15px}.Icon--minimize,.Icon--maximize{fill:inherit;height:20px;width:20px;padding:0}.Icon--export,.Icon--print{height:40px;padding:7px;width:40px}.Icon--indicator{height:20px;margin-right:5px;stroke:inherit;stroke-width:10;width:20px}.Icon--rotated{-webkit-transform:rotate(180deg);transform:rotate(180deg)}

.TextPanel{align-items:center;background-color:#fff;border-radius:5px;box-shadow:0 2px 5px rgba(0,0,0,0.3);display:grid;grid-template-areas:"percent percent" "value   value  " "header  header ";grid-template-columns:1fr auto;grid-template-rows:1fr auto;overflow:hidden;width:100%}.TextPanel--profit{grid-area:profit}.TextPanel--users{grid-area:users}.TextPanel--orders{grid-area:orders}.TextPanel--complaints{grid-area:complaints}.TextPanel__header{align-items:center;background-color:#ddd;display:flex;grid-area:header;justify-content:space-between;width:100%}.TextPanel__heading{font-size:0.9rem;text-transform:uppercase}.TextPanel__heading,.TextPanel__value,.TextPanel__percentage{margin:0;padding:10px}.TextPanel__value{font-size:1.5em;grid-area:value;padding-top:0}.TextPanel__percentage{align-items:center;display:flex;justify-content:center;font-size:1.2em;grid-area:percent;justify-self:end;padding-bottom:0}.TextPanel__percentage--positive{color:#bcd74a;stroke:#bcd74a}.TextPanel__percentage--negative{color:#fa5050;stroke:#fa5050}

.VisualPanel{background-color:#fff;border-radius:5px;box-shadow:0 2px 5px rgba(0,0,0,0.3);position:relative;overflow:hidden}.VisualPanel--histogram{grid-area:histogram}.VisualPanel--map{grid-area:map;position:relative}.VisualPanel--summary{grid-area:summary}.VisualPanel__header{align-items:center;background-color:#fff;display:flex;justify-content:space-between;left:0;position:absolute;top:0;width:100%;z-index:1}.VisualPanel__heading{font-size:1.2em;margin:0;padding:10px;text-transform:uppercase}.VisualPanel__button{background:none;border:none;color:#555;cursor:pointer;font-family:'Nunito';font-size:1em;font-weight:bold;outline:none;padding:0;text-transform:uppercase;align-items:center;display:flex;justify-content:center;fill:#555;padding:5px}.VisualPanel__button:hover{background-color:#aaa;fill:#fff}@media only screen and (max-width: 1100px){.VisualPanel__button{display:none}}

@-webkit-keyframes dropdownMenu{0%{-webkit-transform:scaleY(0);transform:scaleY(0);-webkit-transform-origin:top;transform-origin:top}100%{-webkit-transform:scaleY(1);transform:scaleY(1);-webkit-transform-origin:top;transform-origin:top}}@keyframes dropdownMenu{0%{-webkit-transform:scaleY(0);transform:scaleY(0);-webkit-transform-origin:top;transform-origin:top}100%{-webkit-transform:scaleY(1);transform:scaleY(1);-webkit-transform-origin:top;transform-origin:top}}.Dropdown{position:relative}.Dropdown--period{grid-area:period}.Dropdown--field{grid-area:field}.Dropdown--month{grid-area:month}.Dropdown--year{grid-area:year}.Dropdown__button{background:none;border:none;color:#555;cursor:pointer;font-family:'Nunito';font-size:1em;font-weight:bold;outline:none;padding:0;text-transform:uppercase;align-items:center;border-radius:5px;display:flex;justify-content:space-between;min-width:8em;padding:10px;text-align:left;transition:background-color .2s;width:100%}.Dropdown__button--menu{justify-content:flex-start;border-radius:0;border-radius:initial}.Dropdown__button--year{min-width:6em}.Dropdown__button--active{background-color:#ccc}.Dropdown__button:hover{background-color:#ccc}.Dropdown__label{margin-right:10px}.Dropdown__list{-webkit-animation:dropdownMenu 0.25s;animation:dropdownMenu 0.25s;background-color:#eee;box-shadow:0 2px 5px rgba(0,0,0,0.3);border-radius:5px;left:0;list-style-type:none;margin:0;padding:0;position:absolute;top:100%;width:100%;z-index:2}

.Button{background:none;border:none;color:#555;cursor:pointer;font-family:'Nunito';font-size:1em;font-weight:bold;outline:none;padding:0;text-transform:uppercase;align-items:center;display:flex;justify-content:center;background-color:#ccc;border-radius:5px;justify-content:space-between;min-width:150px;overflow:hidden;text-transform:uppercase;transition:color .2s,background-color .2s}.Button:hover{color:#fff;background-color:#999}@media only screen and (max-width: 600px){.Button{min-width:0}}.Button__text{padding-left:10px}

.Histogram{background-color:#eee;width:100%;height:100%}.tooltip__heading,.tooltip__text{font-size:1rem;margin:0;text-transform:capitalize}.tooltip__heading{margin-bottom:5px}

.Map{align-items:center;display:flex;justify-content:center;width:100%;height:100%;background-color:#eee}.hoverinfo{border:none !important;border-radius:5px !important;box-shadow:0 2px 5px rgba(0,0,0,0.3) !important;font-family:'Nunito', sans-serif;font-size:1rem !important;padding:10px !important}

.App{align-items:center;display:flex;justify-content:center;background-color:#eee;color:#555;display:flex;flex-direction:column;font-weight:normal;height:100%;min-height:100vh;min-width:320px;padding:30px;width:100%}@media only screen and (max-width: 600px){.App{padding:10px}}.App__header,.App__footer{align-items:center;grid-column-gap:20px;-webkit-column-gap:20px;column-gap:20px;display:grid;width:100%}.App__header{grid-area:header}.App__header--stats{grid-template-areas:"heading info period";grid-template-columns:auto 1fr auto}@media (max-width: 1100px){.App__header--stats{grid-template-areas:"heading period" "info    info  ";grid-template-columns:1fr auto}}.App__header--analytics{grid-template-areas:"heading field month year";grid-template-columns:1fr repeat(3, auto)}@media only screen and (max-width: 1100px){.App__header--analytics{grid-template-areas:"heading month year " ".       .     field";grid-template-columns:1fr repeat(2, auto)}}@media only screen and (max-width: 600px){.App__header--analytics{grid-template-areas:"heading field" ".       month" ".       year ";grid-template-columns:1fr auto}}.App__heading{align-self:flex-end;color:#555;font-size:1.6em;grid-area:heading;margin:0}.App__heading--section{align-self:center}.App__info{border-left:1px solid #888;display:flex;flex-direction:column;align-content:flex-start;grid-area:info;margin:0;padding-left:10px}.App__info--current{font-size:1rem;font-weight:bold}.App__info--before{color:#888;font-size:0.8rem}@media only screen and (max-width: 1100px){.App__info{border:none;border-top:1px solid #888;padding:0;padding-top:10px;order:1;grid-column:1 / 3}}.App__section{background-color:#ddd;border-radius:5px;box-shadow:0 2px 20px rgba(0,0,0,0.1) inset,0 2px 5px rgba(0,0,0,0.3) inset;display:grid;grid-gap:20px;gap:20px;max-width:1200px;padding:30px;width:100%}@media only screen and (max-width: 600px){.App__section{padding:15px}}.App__section--stats{grid-template-columns:repeat(4, 1fr);grid-template-areas:"header header header header    " "profit users  orders complaints";margin:30px 0}@media (max-width: 1100px){.App__section--stats{grid-template-columns:repeat(2, 1fr);grid-template-areas:"header header    " "profit users     " "orders complaints"}}@media only screen and (max-width: 600px){.App__section--stats{grid-template-columns:1fr;grid-template-areas:"header    " "profit    " "users     " "orders    " "complaints"}}.App__section--analytics{grid-template-columns:repeat(2, 1fr);grid-template-areas:"header    header" "histogram map   " "summary   map   " "footer    footer";grid-template-rows:auto 300px 250px auto}@media (max-width: 1100px){.App__section--analytics{grid-template-columns:1fr;grid-template-areas:"header    " "histogram " "map       " "summary   " "footer";grid-template-rows:auto 300px 400px 250px auto}}@media only screen and (min-width: 1100px){.App__section--histogramMax{grid-template-areas:"header    header   " "histogram histogram" "summary   map      " "footer    footer   ";grid-template-rows:auto 300px 300px auto}.App__section--mapMax{grid-template-areas:"header    header " "map       map    " "histogram summary" "footer    footer ";grid-template-rows:auto 500px 250px auto}.App__section--summaryMax{grid-template-areas:"header    header " "histogram map    " "summary   summary" "footer    footer "}}.App__footer{grid-area:footer;grid-template-columns:repeat(2, auto);justify-content:end}@media only screen and (max-width: 600px){.App__footer{justify-content:stretch}}

